<template>
  <div class="top">
    <div @click="saoma()" class="saoma">必须先扫码识别设备编号</div>

    <div class="info_wrapper" @click="saoma()">
      <div class="name">
        <span>设备编号</span>
        <span class="red">*</span>
      </div>
      <div class="weizhi">
        <span>{{number}}</span>
      </div>
    </div>

    <div class="info_wrapper" @click="showjiudian=true">
      <div class="name">
        <span>酒店名称</span>
        <span class="red">*</span>
      </div>
      <div class="weizhi">
        <span>{{name_jd}}</span>
      </div>
    </div>
    <div id="nav">
      <van-radio-group v-model="navradio">
          <van-radio name="1" checked-color="#15BDDB">自己</van-radio>
          <van-radio name="2" checked-color="#15BDDB">团队</van-radio>
      </van-radio-group>
    </div>
    <div class="info_wrapper" v-show="navradio=='2'" @click="showdaili_()">
      <div class="name">
        <span>团队</span>
        <span class="red">*</span>
      </div>
      <div class="weizhi">
        <span>{{name_dl}}</span>
      </div>
      <!-- <input type="text" placeholder="请选择代理" v-model="name_dl"/> -->
    </div>



    <!-- 提交 -->
    <div class="submit">
      <button class="btn" @click="getsetin()">绑定</button>
    </div>

    <!-- 酒店列表 -->
    <van-popup v-model="showjiudian" position="bottom">
      <van-picker 
      :columns="jiudian_list"
      @cancel="close_jd"
      @confirm="confirm_jd"
      @change="changejd"
      show-toolbar/>
    </van-popup>

    <!-- 代理列表 -->
    <van-popup v-model="showdaili" position="bottom">
      <van-picker 
      :columns="daili_list"
      @cancel="close_dl"
      @confirm="confirm_dl"
      show-toolbar/>
    </van-popup>

  </div>
</template>

<script>
import { getagent_binding, getagent_trun, getsaoma } from '@/axios/getData'
import { Popup, Radio, Area, RadioGroup, Icon, Toast, Picker, Dialog } from 'vant';
import util from '../utils/utils.js'  
export default {
  data(){
    return{
      jiudian_list:[],    //  酒店列表
      showjiudian:false,  //  酒店弹出层

      daili_list:[],      //  代理列表
      showdaili:false,    //  代理弹出层

      name_jd:'请选择酒店',         //  酒店名
      name_dl:'请选择代理',         //  代理名
      number:'扫码识别设备编号',     //  请输入设备编号
      navradio:'1',                //  提交时对应的字段  1 自己      2 团队
      jd_id:"",                    //  酒店id
      dl_id:"",                    //  代理id
      data:[],                     // 
      type:0,   //  判断是否输入设备编号
    }
  },
  components: {
    vanPopup: Popup,
    vanRadio: Radio,
    vanArea: Area,
    vanRadioGroup: RadioGroup,
    vanIcon: Icon,
    vanPicker: Picker 
  },
  created() {
    this.getdata();
    this.getdata1();
    try {
      var url = window.location.href
      if(url.search('number=')!=-1){
        this.number = url.split('number=')[1].split('&')[0];
        this.type = 1
      }
    } catch (error) {  
    }
  },
  methods:{
    // 点击团队
    showdaili_(){
      if(!this.daili_list.length){
        Dialog.confirm({
          title: '错误提示',
          message: '您还未添加任何代理',
          confirmButtonText:'添加代理',
          cancelButtonText:'返回',
          showCancelButton:true,
        }).then(() => {
          this.$router.push({path:'/add_daili'})
        }).catch(() => {
          
        });
      } else{
        this.showdaili = true
      }
    },
    // 获取酒店与团队列表
    async getdata1(){
      // console.log(123)
      var res = await getagent_trun();
      this.data = res.data;
      if(res.data.hotel_list.length==0){
        Dialog.confirm({
          title: '错误提示',
          message: '您还未添加任何酒店',
          confirmButtonText:'添加酒店',
          cancelButtonText:'返回',
          showCancelButton:true,
        }).then(() => {
          this.$router.push({path:'/add_jiudian'})
        }).catch(() => {
          this.$router.push({path:'/sj_zhongxin'})
        });
      }
      var arr = res.data.hotel_list
      var arr1 = res.data.distributor_list
      arr.forEach(item => {
        this.jiudian_list.push(item.hotel_name)
      });
      arr1.forEach(item1 => {
        this.daili_list.push(item1.username)
      });
    },
    // 点击选择酒店
    changejd(picker, values){
      console.log(picker, values,11)
    },
    close_jd(){
      this.showjiudian = false
    },
    confirm_jd(value,index){
      this.name_jd = value
      this.jd_id = this.data.hotel_list[index].user_id
      this.showjiudian = false      
    },

    close_dl(){
      this.showdaili = false
    },
    confirm_dl(value,index){
      this.name_dl = value
      this.dl_id = this.data.distributor_list[index].user_id
      this.showdaili = false      
    },


    // 数据提交
    getsetin(){
      if(this.navradio=='2'){
        if(!this.dl_id){
          Toast('请完善设备绑定信息');
          return;
        }
      }
      if( this.jd_id&& this.name_jd && this.number ){
        var data = {}
        data.type = this.navradio;          //  1自己   2团队
        data.number = this.number           //  设备名称
        data.hotel_name = this.name_jd      //  酒店名称
        data.agent_user_id = this.dl_id     //  代理id
        data.hotel_user_id = this.jd_id     //  酒店id
        getagent_binding(data).then((res)=>{
          if(res.code==200){
            Toast(res.data);
            this.$router.go(-1)
          } else if(res.code==10000){
            Toast(res.msg);
            // this.$router.go(-1)
          }
        })
      } else {
        Toast('请完善设备绑定信息')
      }
    },
    saoma(){
      wx.ready(() => {
        wx.scanQRCode({
          needResult: 0, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
          desc: "scanQRCode desc",
          scanType: ["qrCode", "barCode"], // 可以指定扫二维码还是一维码，默认二者都有
          success: r => {
            // var str = JSON.parse(JSON.stringify(r));
            // alert(str)
          }
        });
      });
    },
    getdata(){
      var that = this
      getsaoma().then(res => {
        // console.log(res.data)
        let data = res.data;
        let op = {
          debug: false,
          appId: data.appId, //公众号的唯一标识
          timestamp: data.timestamp, // 生成签名的时间戳
          nonceStr: data.nonceStr, // 生成签名的随机串
          signature: data.signature, // 签名
          jsApiList: ["checkJsApi", "scanQRCode", "getLocation"] // 需要使用的JS接口列表(我只需要调用扫一扫的接口，如有多个接口用逗号分隔)
        };
        wx.config(op);
        // that.saoma();
        wx.ready(() => {
          wx.getLocation({
            type: 'gcj02', // 默认为wgs84的gps坐标，如果要返回直接给openLocation用的火星坐标，可传入'gcj02'
            success: function (res) {
              that.wd = res.latitude; // 纬度，浮点数，范围为90 ~ -90
              that.jd = res.longitude; // 经度，浮点数，范围为180 ~ -180。
              var speed = res.speed; // 速度，以米/每秒计
              var accuracy = res.accuracy; // 位置精度
              console.log(res,123)
            }
          });
        });
        wx.error(r => {

        });
      });
    },
  },
  mounted(){

  }
}
</script>

<style scoped lang="scss">
.top{
  padding:20px 15%;
  height:100vh;
  background: #FAFAFA;
}
  .info_wrapper {
    padding: 25px 0;
    box-sizing:border-box;
    border-bottom: 1px solid rgb(238, 238, 238);
    display: flex;
    align-items: center;
    .name{
      width:25%;
      text-align: right;
    }
    .red {
      color: #e62121;
    }
    .weizhi{
      border: 1px solid #DEDEDE;
      margin-left:20px;
      padding:10px;
      box-shadow: 0 0 7px #DEDEDE inset;
      flex:1;
      background: #fff;
    }
    input {
      border: 1px solid #DEDEDE;
      margin-left:20px;
      padding:10px;
      box-shadow: 0 0 7px #DEDEDE inset;
      flex:1;
    }
  }
  input::-webkit-input-placeholder{
    color:#999;
  }
  #shenfen{
    width:50%;
    padding:50px;
    border-radius: 10px;
    .title{
      text-align: center;
      display: inline-block;
      width:100%;
      margin-bottom:20px;
    }
    /deep/ .van-radio{
      margin-bottom:15px;
    }
  }
  #ditu{
    width:101%;
    height:100%;
  }
  .search{
    display: flex;
    padding:5%;
    align-items: center;
    .input{
      width:70%;
      border:1px solid #eee;
      padding:10px;
    }
    .se{
      margin-left:30px;
      padding:10px 30px;
      background: #15BDDB;
      color:#fff;
    }
  }
  .item{
    padding:25px 30px;
    border-bottom:1px solid #eee;
    display: flex;
    align-items: center;
    /deep/ .van-icon{
      margin-right:15px;
    } 
  }
  .submit{
    position: fixed;
    bottom:0;
    // z-index: 1;
    // text-align: center;
    padding:15px 0;
    background: #fff;
    width:100%;
    // border-top:1px solid #eee;
    .btn{
      font-size: 30px;
      width:70%;
      background: #15BDDB;
      color:#fff;
      border-radius: 50px;
      padding:20px 0;
    }
  }
#taocan /deep/ .van-radio__label{
  color:#333;
}
#taocan /deep/ .van-radio{
  margin-bottom: 10px;
  margin-left: 30px;
}
#nav{
  margin-top: 20px
}
/* 头部单选 */
#nav /deep/ .van-radio-group{
  display: flex;
  justify-content: space-around;
}
.weizhi{
  border: 1px solid #DEDEDE;
  margin-left:20px;
  padding:10px;
  box-shadow: 0 0 7px #DEDEDE inset;
  flex:1;
  background: #fff;
}
.saoma{
  margin: 40px 0;
  text-align: center;
  color:#fff;
  font-size: 32px;
  padding:10px;
  background: rgb(7, 193, 96);
  border-radius: 6px;
}
</style>



